import HTML5Game from '../../html5game';

function player(actor:Record<string,string>,game:HTML5Game){
    if(typeof actor.initialize == 'undefined'){
        actor.xspeed = '5';
        actor.yspeed = '-5';
        actor.initialize = '1';
    }

    let x = +actor.x;
    let y = +actor.y;
    let r = +actor.r;
    let xspeed = +actor.xspeed;
    let yspeed = +actor.yspeed;
    let G = .001;

    game.stage.forEach((i) => {
        if(i.name == 'ball'){
            let iX = +i.x;
            let iY = +i.y;
            let iR = +i.r;
            xspeed += (iX - x) * G;
            yspeed += (iY - y) * G;
        }
    });

    x += xspeed;
    y += yspeed;
    
    game.video.drawCircle(x,y,+actor.r);

    actor.x = x+'';
    actor.y = y+'';
    actor.xspeed = xspeed+'';
    actor.yspeed = yspeed+'';
}

export default player;